import React, { Component } from "react";
// import { Link } from "react-router-dom"

import classnames from "classnames/bind";

import css from "./index.scss";
import Bar from "../../components/Bar"
import Title from "../../components/Title"
import Content from "./ContentComponent"
import Instrument from "./InstrumentComponent"

const cx = classnames.bind(css);
const moduleName = "UseComponent";

class UseComponent extends Component {
  constructor() {
    super();
    this.state = {
      
      instrumentData:[

      ]
    }
  }
  componentDidMount () {
    document.documentElement.scrollTop = 0;
  }
  render() {
    return (
      <div className={cx(`${moduleName}`)}>
        <Bar/>
        <Title 
          title=" "
          url1="use"
          url2=""
          urlName1=" "
          urlName2=""/>
        <Content 
          individualTitle=" "
          background="rgb(190, 179, 179)"/>
        <Content individualTitle=" "
          background="rgb(255,255,255)"/>
        <Instrument
          individualTitle=" "
          background="rgb(190, 179, 179)"
          weekTime1="09:00"
          weekTime2="22:00"
          weekendTime1="10:00"
          weekendTime2="17:00"
          breakTime1="12:00"
          breakTime2="13:00"
          methodImage1="/assets/check.png"
          methodTitle1="/ "
          methodContent1=" /        ."
          methodImage2="/assets/use_register.png"
          methodTitle2=" "
          methodContent2="       ."
          methodImage3="/assets/visit_use.png"
          methodTitle3=" "
          methodContent3="    ."
          /> 
      </div>
    )
  }
}
export default UseComponent




